import React, { useEffect,useState } from "react";
import { Card, Avatar,Row,Col,Image,Carousel,Typography,Descriptions, notification } from 'antd';
import { EditOutlined, EllipsisOutlined, SettingOutlined } from '@ant-design/icons';
import Navbar from "./../../Components/Navbar/Navbar"

import Hosts from "./../../Hosts.json"
import Endpoints from "./../../Endpoints.json"
import Preloader from "../../Components/Preloader/Preloader";
import { Redirect } from "react-router";


const { Meta } = Card;
const { Title } = Typography;

let getPokemon = async (id)=>{
    let response = await fetch(Hosts.pokeapi+Endpoints.GetPokemonList+"/"+id)
    if(response.ok){
        let data = await response.json()
        return data;
    }else{
        notification["error"]({message:"Request Failed"})
        return false;

    }
}
function onChange(a, b, c) {
    // console.log(a, b, c);
  }
  
  const contentStyle = {
    height: '160px',
    color: '#fff',
    lineHeight: '160px',
    textAlign: 'center',
    background: '#364d79',
  };    
const Profile = (props)=>{
    let id = props.match.params.id
        let [pstate,setPState] = useState(undefined)
        let [profileImages,setProfileImages] = useState([]);
        let [isok,setIsok] = useState(true)
    useEffect(async ()=>{
        let data = await getPokemon(id)
        if(!data) {return; setIsok(false)}

        let images = []
        
        Object.keys(data.sprites).forEach((eachKey)=>{
            // if( == String)
            if(typeof data.sprites[eachKey] == "string"){
                images.push(data.sprites[eachKey])
            }
        })
        setPState({
            data:data,
            images:images
        })
        
    },[])   
    if(!pstate) return <Preloader status={pstate ? false:true} />
    if(!isok) return <Redirect to="/" />
    // console.log(pstate)
    return <div>
        <Navbar />
        <div>
        <Row style={{textAlign:"center"}}>
            <Col span={12} offset={6}>
                
            {/* <Preloader status={false} /> */}
            {pstate.images.map((eachimage,index)=>{
                return <Image
                key={index}
                width={200}
                height={200}
                src={eachimage}
                fallback="data:image/png;base64,"
                />
            })}
            
            <Title><span style={{textTransform:"capitalize"}}>{pstate.data.name}</span></Title>
            <Descriptions title="Pokemon Characteristics">
                
                {/* {pstate.data.name} */}
                <Descriptions.Item label="Height">{pstate.data.height} unit</Descriptions.Item>
                <Descriptions.Item label="Weight">{pstate.data.weight} unit</Descriptions.Item>
                <Descriptions.Item label="Height">{pstate.data.height}</Descriptions.Item>
            </Descriptions>
            </Col>
        </Row>
        </div>
    </div>
}
export default Profile  